.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 3;
}

.header:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #4c3d74;
  z-index: 3;
}

.header__body {
  position: relative;
  z-index: 3;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 105px;
  transition: padding 0.3s ease;
}

.header.header--active .header__body {
  padding-top: 10px;
  padding-bottom: 10px;
  transition: padding 0.3s ease;
}

.header__heading {
  margin-right: 60px;
  font-weight: 700;
  font-size: 16px;
  line-height: 19px;
}

.header__heading span {
  display: block;
}

@media (max-width: 767.98px) {
  .header__heading a {
    font-size: 14px;
  }
}

.header__burger {
  display: none;
  flex: 0 0 auto;
}

.header__list {
  display: flex;
  flex-wrap: wrap;
}

.header__item:not(:last-child) {
  margin-right: 30px;
}

.header__link {
  position: relative;
  display: inline-block;
  outline: none;
  color: #fff;
  font-size: 16px;
  font-weight: 700;
  text-transform: uppercase;
  text-decoration: none;
}

@media (min-width: 767.98px) {
  .header__link::after {
    content: "";
    position: absolute;
    bottom: -5px;
    left: 0;
    width: 0;
    height: 2px;
    background-color: #fff;
    transition: width 0.3s ease;
  }
  .header__link:hover::after {
    width: 100%;
    transition: width 0.3s ease;
  }
  .header__link:focus::after {
    width: 100%;
    transition: width 0.3s ease;
  }
}

.header__link.active::after {
  width: 100%;
  transition: width 0.3s ease;
}

@media (max-width: 767px) {
  .header__burger {
    display: block;
    position: relative;
    width: 40px;
    height: 20px;
    z-index: 3;
    position: relative;
  }
  .header__burger span {
    position: absolute;
    width: 100%;
    height: 2px;
    background-color: #fff;
    left: 0;
    top: 9px;
    transition: all 0.3s ease 0s;
  }
  .header__burger:before, .header__burger:after {
    content: "";
    position: absolute;
    width: 100%;
    height: 2px;
    background-color: #fff;
    left: 0;
    transition: all 0.3s ease 0s;
  }
  .header__burger:before {
    top: 0;
  }
  .header__burger:after {
    bottom: 0;
  }
  .header__burger.active span {
    transform: scale(0);
  }
  .header__burger.active:before {
    transform: rotate(45deg);
    top: 9px;
  }
  .header__burger.active:after {
    transform: rotate(-45deg);
    bottom: 9px;
  }
  .header__menu {
    position: fixed;
    top: -100%;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #a387c4;
    padding: 95px 57px 20px 57px;
    transition: all 0.3s ease 0s;
    overflow: auto;
  }
  .header__menu.active {
    top: 0;
  }
  .header__menu.menu--onscroll {
    padding: 20px 57px 20px 57px;
  }
  .header__list {
    display: block;
  }
  .header__item {
    margin: 0 0 20px 0px;
  }
  .header__link {
    font-size: 26px;
  }
}

