.accordion-triger {
  position: relative;
  padding-right: 60px;
  width: 100%;
  background: transparent;
  color: #4c3d74;
  text-align: left;
  transition: transform 0.3s ease;
}

@media (min-width: 767.98px) {
  .accordion-triger:hover {
    transform: scale(1.01);
  }
  .accordion-triger:focus {
    transform: scale(1.01);
  }
}

.accordion-triger::after {
  content: "";
  position: absolute;
  top: 50%;
  right: 10px;
  width: 16px;
  height: 2px;
  background-color: #4c3d74;
  transform: translateY(-50%);
  transition: transform 0.3s ease;
}

.accordion-triger::before {
  content: "";
  position: absolute;
  top: 50%;
  right: 17px;
  width: 2px;
  height: 16px;
  background-color: #4c3d74;
  transform: translateY(-50%);
  transition: transform 0.3s ease;
}

.accordion--active .accordion-triger::after {
  transform: translateY(-50%) rotate(180deg);
}

.accordion--active .accordion-triger::before {
  transform: translateY(-50%) rotate(90deg);
}

