.snackbar {
  position: fixed;
  bottom: 30px;
  right: 50px;
  z-index: 10;
  width: auto;
  min-width: 200px;
  padding: 15px;
  visibility: hidden;
  border-radius: 2px;
  color: #fff;
  text-align: center;
}

@media (max-width: 767.98px) {
  .snackbar {
    right: 50%;
    transform: translateX(50%);
  }
}

.snackbar-btn {
  transition: transform 0.3s ease, opacity 0.3s ease;
}

.snackbar-btn:disabled {
  cursor: default;
  opacity: 0.5;
  transition: opacity 0.2s ease;
}

.snackbar.show-notification {
  visibility: visible;
  background-color: #333;
  /* Если 3000 в js, то в стилях 2.6s */
  animation: fadein 0.5s, fadeout 0.5s 2.6s;
}

.snackbar.show-attention {
  visibility: visible;
  background-color: #ff6060;
  /* Если 3000 в js, то в стилях 2.6s */
  animation: fadein 0.5s, fadeout 0.5s 2.6s;
}

@keyframes fadein {
  from {
    bottom: 0;
    opacity: 0;
  }
  to {
    bottom: 30px;
    opacity: 1;
  }
}

@keyframes fadeout {
  from {
    bottom: 30px;
    opacity: 1;
  }
  to {
    bottom: 0;
    opacity: 0;
  }
}

/*  Объект для модального окна, чтобы страница не прокручивалась вверх  */
.fixed {
  position: fixed;
  top: 0;
  left: 0;
  opacity: 0;
}

/* ----- */
.popup {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10;
  width: 100%;
  height: 100%;
  overflow-y: auto;
  overflow-x: hidden;
  opacity: 0;
  visibility: hidden;
  backface-visibility: hidden;
  background-color: rgba(0, 0, 0, 0.726);
  transition: all 0.7s ease;
}

.popup.open {
  opacity: 1;
  visibility: visible;
}

.popup.open .popup__content {
  opacity: 1;
  transform: translate(0px, 0px);
}

.popup__wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 90vh;
  padding: 30px 10px;
}

.popup__content {
  position: relative;
  display: flex;
  flex-direction: column;
  max-width: 600px;
  padding: 30px;
  border-radius: 30px;
  background-color: #f2eef7;
  opacity: 0;
  color: #4c3d74;
  transform: translate(0px, -100%);
  transition: all 0.7s ease;
}

.popup__content form {
  display: flex;
  flex-direction: column;
}

.popup__content img.popup__image {
  width: 100%;
}

.popup__close {
  position: absolute;
  top: 0;
  right: 15px;
  display: block;
  align-self: flex-end;
  background: transparent;
  color: #4c3d74;
  font-size: 32px;
  transition: all 0.2s ease-in;
}

.popup__close:hover {
  transform: scale(1.2);
}

.popup__title {
  margin-bottom: 20px;
  font-weight: 700;
  text-align: center;
  font-size: 28px;
}

@media (max-width: 479.98px) {
  .popup__title {
    font-size: 20px;
  }
}

.popup__close-image {
  width: 15px;
}

.popup__desc {
  font-weight: 700;
  text-align: center;
  margin-bottom: 20px;
}

@media (max-width: 479.98px) {
